var timer = null
import {
	MYCASH,
	CASHLIST
} from "../../../utils/api.js"
import uniLoadMore from '../../../components/uni-load-more/uni-load-more.vue'
export default {
	components: {
		uniLoadMore
	},
	data() {
		return {
			myWalletData: "",
			mineWithDraw: "",
			cashListData: '',
			loadingText: 'more',
			page: 1
		}
	},
	filters:{
		typeFormate:function(value){
			var result
			if(value == 1){
				result = '普通邀请会员首次购买'
			} else if(value ==2 ){
				result = '普通邀请会员续费购买'
			} else if (value ==3){
				result = '合伙人邀请会员首次购买'
			} else if(value ==4){
				result = '合伙人邀请会员续费购买'
			} else if(value ==5){
				result = '合伙人提现'
			} else if(value ==6){
				result = '合伙人提现失败'
			} else if(value ==7){
				result = '普通收益提现'
			} else if(value ==8){
				result = '普通收益提现失败'
			} else if(value ==9){
				result = '卡密收益'
			}
			return result
		}
	},
	onShow() {
		uni.showToast({
			title: '加载中...',
			icon: 'none',
			mask: true
		})
		// 初始化数据
		this.initData()
		// 零钱明细
		this.cashlist()
	},
	onLoad() {},
	onPullDownRefresh() {
		this.initData()
		this.cashlist()
	},
	onReachBottom: function() {
		var _this = this
		if (timer != null) {
			clearTimeout(timer);
		}
		timer = setTimeout(function() {
			_this.getmore();
		}, 1000);
	},
	methods: {
		// 初始化数据
		initData() {
			const params = {}
			this.$request.url_request(MYCASH, params, "GET", res => {
				this.myWalletData = JSON.parse(res.data).data
				this.mineWithDraw = JSON.parse(res.data).data.balance
				uni.hideToast()
				uni.stopPullDownRefresh()
			}, err => {})
		},
		// cashlist 零钱明细
		cashlist() {
			const params = {
				page: 1
			}
			this.$request.url_request(CASHLIST, params, "GET", res => {
				this.cashListData = JSON.parse(res.data).data.records
				uni.stopPullDownRefresh()
			}, err => {})
		},
		// 加载更多
		getmore() {
			if (this.loadingText == 'more') {
				this.loadingText = 'loading'
				this.$forceUpdate()
				this.page++
				const params = {
					page: this.page
				}
				this.$request.url_request(CASHLIST, params, "GET", res => {
					if (JSON.parse(res.data).data.records.length <= 0) {
						uni.hideNavigationBarLoading();
						this.loadingText = 'noMore'
						this.$forceUpdate()
					} else {
						this.loadingText = 'more'
						this.$forceUpdate()
						this.cashListData = this.cashListData.concat(JSON.parse(res.data).data.records)
					}
				}, err => {})
			}


		},
		walletDetailClick() {
			uni.navigateTo({
				url: '../MineWalletDetail/MineWalletDetail'
			})
		},
		// 提现
		withDrawClick() {
			uni.navigateTo({
				url: "../MineWithDraw/MineWithDraw"
			})
		},
		goback() {
			uni.navigateBack({
				delta: 1
			})
		}
	}
}
